: relative;\n  user-select: none;\n  flex: 1 1 0%;\n  flex-direction: row;\n  display: flex;\n  font-family: Verdana, Tahoma, sans-serif;\n  background-color: $plot_bg;\n  pointer-events: none;\n  overflow: hidden;\n  .track {\n    border-right: 1px solid $plot_border_color;\n    overflow: hidden;\n    padding: 0;\n    display: flex;\n    flex-direction: column;\n    .track-title {\n      display: flex;\n      justify-content: center;\n      align-items: center;\n      font-family: sans-serif;\n      font-weight: bold;\n      overflow: hidden;\n      background-color: $plot_title_bg;\n      color: $plot_title_color;\n      text-align: center;\n      text-anchor: middle;\n      white-space: nowrap;\n      border-bottom: 1px solid $plot_border_color;\n      pointer-events: auto;\n      cursor: help;\n    }\n    .track-legend {\n      pointer-events: all;\n      overflow: hidden;\n      border-bottom: 1px solid $plot_border_color;\n    }\n    .track-container {\n      flex: 1 1 auto;\n      overflow: hidden;\n      canvas,svg {\n        overflow: hidden;\n      }\n    }\n  }\n  .track:last-child {\n    border-right: none;\n  }\n  .track .error {\n    background: repeating-linear-gradient(\n      -45deg,\n      #f99,\n      #f99 10px,\n      #fff 10px,\n      #fff 20px\n    );\n  }\n  .overlay {\n    pointer-events: auto;\n    min-height: 0;\n    position: absolute;\n    z-index: 200;\n  }\n\n  .hidden {\n    display: none !important;\n  }\n}\n\n.log-controller.horizontal {\n  flex-direction: column;\n  .track {\n    flex-direction: row;\n    border-right: none;\n    border-bottom: 1px solid $plot_border_color;\n    .track-title {\n      writing-mode: vertical-lr;\n      transform: rotate(180deg);\n      border-bottom: none;\n      border-left: 1px solid $plot_border_color;\n    }\n    .track-legend {\n      border-bottom: none;\n      border-right: 1px solid $plot_border_color;\n    }\n  }\n  .track:last-child {\n    border-bottom: none;\n  }\n}\n","$scale_master_color: #555;\n$scale_minor_color: #777;\n$scale_slave_scale_bg: #f9f9f9;\n$scale_bg: white;\n\n\n.scale-track {\n  stroke: $scale_master_color; }\n  .scale-track .label-bg {\n    stroke: $scale_bg;\n    fill: $scale_bg;\n}\n\n.slave-scale {\n  background-color: $scale_slave_scale_bg;\n}\n\n.slave-scale .label-bg {\n  stroke: $scale_slave_scale_bg;\n  fill: $scale_slave_scale_bg;\n}\n\n.slave-scale text {\n  stroke: $scale_minor_color;\n  fill: $scale_minor_color;\n}\n\n.slave-scale .major-tick-line {\n  stroke-width: 0.5;\n  stroke: $scale_minor_color;\n}\n\n.master-scale .major-tick .label-bg {\n  stroke: $scale_bg;\n  fill: $scale_bg;\n}\n\n.master-scale .major-tick text {\n  stroke: $scale_master_color;\n  fill: $scale_master_color;\n}\n\n.master-scale .minor-tick-line {\n  stroke-width: 0.5;\n  stroke: $scale_master_color;\n}\n\n","// These are important to properly display the component\n@import '@equinor/videx-wellog/dist/styles/log-styles.scss';\n//@import '@equinor/videx-wellog/dist/styles/loader-styles.scss';\n@import '@equinor/videx-wellog/dist/styles/scale-styles.scss';\n\n\n//$scale_slave_scale_bg: #ff0000;\n\n.welllogview {\n  width: 100%;\n  height: 100%;\n  display: flex;\n\n  // redefine videx-wellog log-controller\n  height: inherit;\n\n  .track {\n    .track-title {\n      background-color: #ccc;\n      font-weight: normal;\n      color: black;\n      font-family: inherit;\n      white-space: nowrap;\n      display: block;\n      text-overflow: ellipsis;\n      overflow: hidden;\n    }\n    .track-legend {\n      /*.scale-title {\n        display: block;\n        text-overflow: ellipsis;\n        overflow: hidden;\n      }*/\n    }\n  }\n\n  .selected {\n    background-color: rgba(255, 155, 79, 0.1);\n  }\n\n  .slave-scale {\n    background-color: rgba(255, 255, 255, 0.0235); // to make track selection background visible\n  }\n\n  .track-container {\n    pointer-events: all; // to allow mouse events handling\n  }\n\n  .overlay {\n    .depth {\n      //not work?? position : absolute;\n      right: 5px;\n      bottom: 5px;\n      display: inline-block;\n\n      padding: 2px;\n      border-radius: 4px;\n      text-align: right;\n      background-color: rgba(0, 0, 0, 0.5);\n      color: white;\n    }\n\n    .rubber-band {\n      background-color: rgba(255, 0, 0, 0.1);\n\n      div {\n        background-color: rgba(255, 0, 0, 0.7);\n      }\n    }\n\n    .pinned {\n      background-color: rgba(0, 0, 0, 0.1);\n\n      div {\n        background-color: rgba(0, 255, 0, 0.7);\n      }\n    }\n  }\n\n  .title {\n    color: rgb(0, 0, 0);\n    background-color: rgb(241, 241, 241);\n    font-size: 14px;\n    text-align: center;\n    flex: \"0, 0\",\n  }\n  .title-horizontal {\n    writing-mode: vertical-lr;\n    transform: rotate(180deg);\n  }                          \n\n  .view {\n    width: 100%;\n    height: 100%;\n    flex: 1, 1;\n    display: flex;\n    flex-direction: column;\n\n    container { \n      flex: 1, 1;\n    }\n  }  \n\n  .error {\n    flex: 0, 0;\n    color: rgb(255, 64, 64);\n    background-color: rgb(255, 255, 192)\n  }\n\n  .wellpick {\n    font-size: 9px;\n\n    .vertical-text {\n      writing-mode: vertical-lr;\n      transform: rotate(180deg);\n    }\n  }\n\n  .wellpick-fill {\n    opacity: 0.3;\n  }\n\n  .wellpick-pattern {\n    opacity: 0.57\n  }\n}\n\n\n.welllogspacer {\n  width: 100%;\n  height: 100%;\n  display: flex;\n\n  .welllogspacer-distance-horizontal {\n    writing-mode: vertical-lr;\n    transform: rotate(180deg);\n  }\n  .distance { \n    font-size: 12;\n    text-align: center;\n  }\n\n  .wellpick {\n    opacity: 0.9\n  }\n\n  .wellpick-fill {\n    opacity: 0.3\n  }\n\n  .wellpick-pattern {\n    opacity: 0.57\n  }\n}\n\n"],"sourceRoot":""}]);
